.App {
  text-align: center;
}

.gallery-container {
  margin: 0 150px;
}

h1 {
  font-size: xxx-large;
}

hr {
  margin: 25px 150px;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -15px;
}

.navbar a {
  margin: 0 25px; /* Adjust spacing between items */
  text-decoration: none;
  color: #333;
}

.navbar span {
  color: #333;
}

@media only screen and (max-width: 768px) {
  .gallery-container {
    margin: 0 10px;
  }
  hr {
    margin: 25px 10px;
  }
}